import { defineComponent } from "vue";
import BlockProperties from "@/components/BlockProperties/index.vue";
import EventEmitter from "@/util/EventEmitter";

export default defineComponent({
  name: "EditorBlockComponent",
  props: {
    id: Number,
    description: String,
    logo_class: String,
    nodeData: Object,
    nuid: String,
    key: String,
  },
  components: {
    BlockProperties,
  },
  computed: {
    blockTypeDescription() {
      let result = "";
      switch (this.blockType) {
        case "TRG":
          result = "Trigger";
          break;
        case "OPR":
          result = "Operation";
          break;
        case "LOG":
          result = "Logic";
          break;
        case "ACT":
          result = "Action";
          break;
      }
      return result;
    },
  },
  data() {
    return {
      showProperties: true,
      context: {} as any,
      nodeName: "",
      infoUrl: "",
      blockType: "",
      output_vars: [],
      parameters: [],
      botContext: {} as any,
      blockOnError: false,
      blockTypeId: "",
      uid: this.$props.nodeData ? this.$props.nodeData.id : null,
      detailsCollapsed: false,
      refresh: 0,
    };
  },
  mounted() {
    this.botContext = {};
    this.context = {};
    const id = this.id;
    const nuid = this.nuid;
    setTimeout(() => {
      EventEmitter.emitter.emit("editor-block-data-request", nuid, id);
    }, 40);
    EventEmitter.emitter.on(`editor-block-data-response-${nuid}`, this.setCtx);
    EventEmitter.emitter.on("block-properties-error", this.setError);
    EventEmitter.emitter.on(`editor-block-update-block-response-${this.id}`, (block: any) => {
      this.blockType = block.block_type;
      this.parameters = block.parameters;
      this.output_vars = block.output_vars;
      this.infoUrl = block.info_url;
      // TODO: Errors in update?
      EventEmitter.emitter.emit("editor-block-update-block-updated", nuid, true, block);
    });
  },
  watch: {
    uid: {
      handler(val) {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        EventEmitter.emitter.emit("editor-block-data-ctx-add", this.uid, this.output_vars);
        EventEmitter.emitter.on(`editor-block-bot-context-update-${val}`, this.setBotContext);
      },
    },
  },
  methods: {
    filterCtx(context: any, nodes: any) {
      const result: any = [];
      for (let x = 0; x < context.length; x++) {
        const c = context[x].value;
        if (nodes.indexOf(c.substring(1, c.indexOf("."))) > -1) {
          result.push(context[x]);
        }
      }
      return result;
    },
    setBotContext(context: any, nodes: any) {
      this.botContext = this.filterCtx(context, nodes);
    },
    updateBlock() {
      this.refresh = 1;
      EventEmitter.emitter.emit("editor-block-update-block", this.id, this.blockTypeId);
    },
    setCtx(ctx: any) {
      if (ctx) {
        this.context = {};
        for (const key in ctx.data) {
          this.context[key] = ctx.data[key];
        }
        this.nodeName = ctx.nodeName;
        this.uid = ctx.id;
        this.infoUrl = ctx.infoUrl;
        this.blockType = ctx.blockType;
        this.parameters = ctx.parameters;
        this.output_vars = ctx.output_vars;
        this.blockTypeId = ctx.name;
      }
    },
    setError(blockId: string, error: boolean) {
      if (blockId === this.uid) this.blockOnError = error;
    },
    toggleSettings(st: boolean) {
      if (st) {
        this.showProperties = st;
      } else {
        this.showProperties = !this.showProperties;
      }
      this.detailsCollapsed = !this.detailsCollapsed;
    },
  },
});
