import { defineComponent, reactive, getCurrentInstance } from "vue";
import EventEmitter from "@/util/EventEmitter";
import TextProperty from "@/components/TextProperty/index.vue";
import useVuelidate from "@vuelidate/core";
import { required, email } from "@vuelidate/validators";

const context: unknown = {};

export default defineComponent({
  name: "PropertiesComponent",
  props: {
    parameters: [],
    blockId: String,
    ctx: Object,
    show: Boolean,
    botContext: [],
    uid: Number,
    nuid: String,
    blockType: String,
    refresh: Number,
  },
  setup() {
    const tagifyStuff = reactive({
      value: "",
      tagifySettings: {
        autoComplete: {
          enabled: true,
        },
        duplicates: true,
        tagTextProp: "value",
        pattern: "#",
        mode: "mix",
        enforceWhitelist: true,
        editTags: {
          keepInvalid: false,
        },
        templates: {
          dropdownItem,
        },
        transformTag,
        whitelist: [],
        dropdown: {
          enabled: 0,
          maxItems: 100,
          position: "text",
          mapValueTo: "value",
          highlightFirst: true,
          placeAbove: true,
        },
      },
    });

    function dropdownItem(tagData: any) {
      return `<div class="tagify__dropdown__item">
        <i class="fas fa-database"></i>
        <span class="ml-2">${tagData.value}</span>
      </div>`;
    }

    function transformTag(tagData: any) {
      tagData.style = "--tag-bg: #aee8be";
    }

    return {
      tagifyStuff,
      v$: useVuelidate(),
    };
  },
  mounted() {
    this.tagifyStuff.tagifySettings.whitelist = [];
    this.context = {};
    this.form = {};
    this.validations = {};
    EventEmitter.emitter.on(
      `block-properties-remove-tag-from-field-${this.$props.blockId}`,
      (block: string, data: any) => {
        this.removeTagValueFromField(data.id.substring(0, data.id.indexOf("-")), data.value);
      }
    );
  },
  validations() {
    return this.validations;
  },
  data() {
    return {
      context,
      form: {} as any,
      validations: {},
    };
  },
  components: {
    TextProperty,
  },
  watch: {
    botContext: {
      handler(options) {
        // removeTags from white list
        if (options.length && options.length > 0) {
          this.tagifyStuff.tagifySettings.whitelist = options?.map(function (item: any) {
            return typeof item == "string" ? { value: item } : item;
          });
        } else {
          this.tagifyStuff.tagifySettings.whitelist = [];
        }
      },
    },
    parameters: {
      handler(params) {
        if (params && params.length !== 0) {
          const validations: any = {
            form: {},
          };
          const params: any = this.$props.parameters;
          for (const param of params) {
            // TODO: workaround while solving an issue in the backend
            if (param.mandatory && param.type !== "BOO") {
              validations["form"][param.slug] = { required };
            }
            if (param.type === "BOO") {
              this.form[param.slug] = this.form[param.slug] || true;
            } else if (param.type === "LIS") {
              this.form[param.slug] = this.form[param.slug] || param.placeholder;
            }
            EventEmitter.emitter.emit("block-properties-change", this.$props.blockId, {
              field: param.slug,
              value: this.form[param.slug],
              error: false,
              params,
              blockUuid: this.nuid,
              blockType: this.blockType,
            });
            EventEmitter.emitter.emit(
              `block-properties-update-change-${param.slug}-${this.nuid}`,
              this.$props.blockId,
              this.form[param.slug]
            );
          }
          this.validations = validations;
          setTimeout(this.sendErrorStatus, 100);
        }
      },
    },
    ctx: {
      handler(ctx) {
        this.form = ctx;
      },
    },
  },
  methods: {
    removeTagValueFromField(field: string, value: string) {
      if (this.form[field]) {
        this.form[field] = this.form[field].replace(value, "");
      }
      this.v$.$touch();
      this.validateField(field, value);
    },
    validateField(field: string, value: string) {
      EventEmitter.emitter.emit("block-properties-change", this.$props.blockId, {
        field,
        value,
        error: this.v$.$error,
        params: this.$props.parameters,
        blockUuid: this.nuid,
        blockType: this.blockType,
      });
      EventEmitter.emitter.emit("block-properties-error", this.$props.blockId, this.v$.$error);
    },
    getTagMatchesForField(field: string) {
      const regex = /\[\[\{"value":"(.*?)","text":/gm;
      const result = [];
      let m;
      while ((m = regex.exec(field)) !== null) {
        // This is necessary to avoid infinite loops with zero-width matches
        if (m.index === regex.lastIndex) {
          regex.lastIndex++;
        }
        result.push({
          value: m[0],
          tag: m[1],
        });
      }
      return result;
    },
    updateField(field: number, blockId: string, value: string) {
      if (blockId === this.blockId) {
        this.form[field] = value;
        EventEmitter.emitter.emit("block-properties-change", this.$props.blockId, {
          field,
          value,
          error: this.v$.$error,
          params: this.$props.parameters,
          blockUuid: this.nuid,
          blockType: this.blockType,
        });
        EventEmitter.emitter.emit("block-properties-tag-removal", this.$props.blockId);
      }
    },
    sendErrorStatus() {
      this.v$.$touch();
      EventEmitter.emitter.emit("block-properties-error", this.blockId, this.v$.$error);
    },
    change(ev: any) {
      let value = "";
      let id = "";
      if (ev.target) {
        value = ev.target.type === "checkbox" ? ev.target.checked : ev.target.value;
        id = ev.target.id;
      } else {
        value = ev.value;
        id = ev.id;
      }
      id = id.substring(0, id.indexOf("-"));
      this.form[id] = value;
      this.validateField(id, value);
    },
  },
});
